@mixin font-settings($size, $height, $weight) {
    font-size: $size;
    line-height: $height;
    font-weight: $weight;
}

@mixin bg-image($path: '', $size: cover, $repeat: no-repeat, $pos: center) {
    background-image: url($path);
    background-size: $size;
    background-repeat: $repeat;
    background-position: $pos;
}